.popupwrapper{
	position:fixed;
	top:0;
	left:0;
	width:100%; 
	min-height:100%;
	background-color: rgba(0,0,0,0.4); 
	overflow:auto;
	z-index:999;
}

.store-popup{
	width:100%;
	top:100px;
	position:absolute;
	left:0;
	right:0;
	margin-left:auto;
	margin-right:auto;
	background:white;
	border:0px;
}

.popupimg{
	width : 95%;
	height: 60%;
	border:1px solid rgba(0,0,0,0.2);
}

.btndiv{
	background:#ee7047;
	width:100%;
	height: 40px;
	padding-top:5px;
}

.popup-remove-btn{
	cursor:pointer;
	width : 40px;
	height: 100%;
	border : 0px;
	background : #ee7047;
	outline :none;
	float:right;
	font-size:16px;
	color:white;
	}

.popup-content{
	padding-left:15px;
}

.popupinfo{
	
	width:90%;
	padding:15px 0px;
	padding-bottom:30px;
}

.popupinfo tr,td,th{
	width:10%;
	text-align : left;
	color:black;
}

@media screen and (min-width:768px){
	.store-popup{
		width:350px;
		max-width:686px;
	}
	
	.popupimg{
		margin:0 auto;
		max-width : 95%;
	}
	
	.store-popup{
	top:150px;
	position:absolute;
	left:0;
	right:0;
	margin-left:auto;
	margin-right:auto;
	
}
	
}
