.container{
	margin : 0 auto;
	padding: 0;
	-webkit-text-size-adjust: none;
	min-width : 320px;
	max-width : 980px;	
	overflow:auto;
	height:100%;
	background:white;
}

.categorynav{
	
	border-top:1px solid rgba(0,0,0,0.2);
	border-bottom:1px solid rgba(0,0,0,0.2);
	display : table;
	width : 100%;
	background : white;
	height: 40px;
	table-layout: fixed;
	border-collapse : collapse;
	margin-bottom:30px;
}

.map img, .map a, .map canvas{
	-webkit-tap-highlight-color : transparent;
	outline:none;
}

.floorstorelist{
	border-top:1px solid rgba(0,0,0,0.2);
}

.guide{
	margin:0 auto;
	padding:25px 0px 20px;
	font-size:31px;
	font-weight:300;
	border-bottom:1px solid rgba(0,0,0,0.2);
	-webkit-text-size-adjust:100%;
	   -moz-text-size-adjust:100%;
	    -ms-text-size-adjust:100%;
	        text-size-adjust:100%;
	line-height:31px;
	text-align:center;
}

.search{
	width : 90%;
	height: 40px;
	border : 1px solid #ee7047;
	background : white;
	margin:0 auto 20px;
}

.search input{
	width : 73.125%;
	padding : 10px;
	height:38px;
	border :0px;
	outline:none;
	float:left;
	font-size : 16px;
}

.search button{
	width : 60px;
	height: 100%;
	border : 0px;
	background : #ee7047;
	outline :none;
	float:right;
	font-size:16px;
	color:white;
}
